.card {
    background: #fff;
    border-radius: 2px;
    /*height: 325px;*/
    margin: 1rem;
    position: relative;
    /*width: 50px;*/
    -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
            box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: center;
    border-radius: 100%;
}

.card>.img-container {

    height: 33%;
    border-radius: 50%;
    /*overflow: hidden;*/
    /*text-align: center;*/
    background: #6CADDC;

}

.card>.img-container>img {

    width: 33%
}

.card>.content {

    padding-left: 1rem;

    padding-right: 1rem;
    font-size: 15px;
}

.card>.content>ul {

    list-style-type: none;

}

.card>.content>ul>li {

    margin-top: px;

}

.card>.remove {

    color: red;

    font-size: 24px;

    font-family: lucida calligraphy;

    position: absolute;

    top: -0px;

    right: 125px;

    cursor: pointer;

}

.card>.remove:hover {

    color: #d50000;

}



